import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {request, RestApiProvider} from "../../providers/rest-api/rest-api";
import {BarcodeScanner, BarcodeScannerOptions} from "@ionic-native/barcode-scanner";
import {GoodReceiveDetailPage} from "../good-receive-detail/good-receive-detail";
import moment from "moment";
import * as _ from 'lodash';
import {TranslateService} from "@ngx-translate/core";
import {HTTP} from "@ionic-native/http";

/**
 * Generated class for the GoodReceivePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

export interface GoodReceive {
    poHeaderId: string,
    sapPoHeaderId: string,
    statusDesc: string,
    statusId: string,
    vendorId: string,
    vendorName: string,
}

@IonicPage()
@Component({
    selector: 'page-good-receive',
    templateUrl: 'good-receive.html',
})
export class GoodReceivePage {

    filterKeyword: string = '';
    goodReceives: GoodReceive[] = [];

    constructor(public navCtrl: NavController,
                private barcodeScanner: BarcodeScanner,
                public translate: TranslateService,
                public restApi: RestApiProvider,
                public http: HTTP,
                public navParams: NavParams) {
    }


    filterItem = (item: GoodReceive) => {
        return item.sapPoHeaderId.indexOf(this.filterKeyword) !== -1
    }


    ionViewWillEnter() {
        this.loadData();
    }

    showGoodReceiveItem(goodReceive: GoodReceive) {
        this.navCtrl.push(GoodReceiveDetailPage, {
            data: _.pick(goodReceive, ['sapPoHeaderId', 'poHeaderId']),
            goodReceive: goodReceive
        })
    }

    loadData() {
        this.restApi.post(
            'api/goodsReceives',
            {
                start: 0,
                length: 0
            }).then(response => {
            // console.log(JSON.stringify(response));
            const data = response.data;
            this.goodReceives = data.goodReceives;
            // console.log(data)
            // console.log(this.goodReceives)

        }).catch(error => {
            console.log('error')
            console.log(JSON.stringify(error));
        })
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad goodReceivesPage');
    }

    parseCode(code = ''): any {
        code = String(code);
        if (code.length === 10) {
            return {
                sapPoHeaderId: code
            }
        } else if (code.length === 32) {
            return {
                sapPoHeaderId: code.substring(0, 10),
                plant: code.substr(10, 4),
                shipingDate: moment(code.substr(14, 8), 'YYYYDDMM').format('YYYY-MM-DD'),
                customer: code.substr(22, 10),
            }
        } else {
            return false;
        }
    }

    async scanRequest() {
        this.filterKeyword = await this.barcodeScanner.scan(<BarcodeScannerOptions>{
            orientation: 'landscape',
            showTorchButton: true,
            showFlipCameraButton: true,
            prompt: this.translate.instant('Scan PO ID or Shipping NO')
        }).then(barcodeData => {
            return barcodeData.text;
        }).catch(err => {
            alert('can not scan');
            return '';
        });

        let data = this.parseCode(this.filterKeyword);

        // if (data === false) {
        //     return alert('invalid code');
        // }

        // data = {
        //     poHeaderId: '311800000160',
        //     sapPoHeaderId: '4800000379'
        // }

        // const item = await request({
        //     method: 'POST',
        //     url: 'api/goodsReceives/getPoItem',
        //     data
        // }).then((response) => {
        //     return response.data && response.data.data;
        // }).catch(error => {
        //     return {}
        // });
        //
        // if (_.isEmpty(item)) {
        //     return alert('GR Item not fount');
        // }


        this.navCtrl.push(GoodReceiveDetailPage, {
            data
        })
    }
}



