import {Component} from '@angular/core';
import {AlertController, IonicPage, ModalController, NavController, NavParams, ViewController} from 'ionic-angular';
import {request, RestApiProvider} from "../../providers/rest-api/rest-api";
import {GoodReceiveDetailSavePage} from "../good-receive-detail-save/good-receive-detail-save";
import {TranslateService} from "@ngx-translate/core";
import * as _ from 'lodash';

/**
 * Generated class for the GoodReceiveDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

export interface GoodReceiveItem {
    "batch": string,
    "bwart": string,
    "currency": string,
    "deliveryDate": string,
    "itemTextPo": string,
    "ivQty": number,
    "mark": string,
    "materialDesc": string,
    "materialId": string,
    "plantDesc": string,
    "plantId": string,
    "poItem": string,
    "poQty": number,
    "reQty": number,
    "receiveQty": number,
    "remark": string,
    "sapPoHeaderId": string,
    "shipingNoItem": string,
    "sloc": string,
    "unit": string,
}

export interface GoodReceive {
    conditionHeader?: any,
    conditionItmes?: any[],
    header?: any,
    information?: any,
    items: any[],
    poText?: {
        "delivery": string,
        "headerNote": string,
        "headerText": string,
        "id": string,
        "mode": string,
        "warranties": string,
    },
    setDefault?: any

}

@IonicPage()
@Component({
    selector: 'page-good-receive-detail',
    templateUrl: 'good-receive-detail.html',
})
export class GoodReceiveDetailPage {

    loading: boolean = false;
    goodReceive: GoodReceive;
    filterDate: string = '';
    dateOptions: string[] = [];

    data: {
        poHeaderId: string,
        sapPoHeaderId: string
    };

    constructor(public navCtrl: NavController,
                public alertCtrl: AlertController,
                public modalCtrl: ModalController,
                public translate: TranslateService,
                public restApi: RestApiProvider,
                private viewCtrl: ViewController,
                public navParams: NavParams) {
        this.data = navParams.get('data') || {};
        this.goodReceive = navParams.get('goodReceive') || {};
        console.log('get data', this.data);
    }

    filterItem = (item: GoodReceiveItem) => {
        return this.filterDate === '' || item.deliveryDate === this.filterDate
    }


    ionViewWillEnter() {
        this.loadData();
    }

    async loadData() {

        // this.viewCtrl.showBackButton(false);
        this.loading = true;
        const params = _.pick(this.data, [
            'sapPoHeaderId',
            'plant',
            'shipingDate',
            'customer'
        ]);
        const data = await this.getPoItems(params);


        if (_.isEmpty(data)) {
            const alert = this.alertCtrl.create({
                title: this.translate.instant('Error'),
                message: this.translate.instant('GoodReceive not found'),
            })
            alert.onWillDismiss(() => {
                this.navCtrl.pop();
            })
            return alert.present();

        } else {
            _.merge(this.goodReceive, data);
            this.dateOptions = _.uniq(_.map(data.items, item => {
                return item.deliveryDate
            })) || [];
            console.log(this.dateOptions);
        }
        this.loading = false;
    }

    async getPoItems(data) {
        return await this.restApi.post(
            'api/goodsReceives/getPoItem',
            data
        ).then((response) => {
            return response && response.data
        }).catch(error => {
            return {}
        });

        // if (_.isEmpty(item)) {
        //     return alert('GR Item not fount');
        // }
        // return await request({
        //     method: 'GET',
        //     url: 'api/purchasingOrders/' + po_id,
        // }).then(response => {
        //     return response && response.data && response.data.data;
        // })
    }

    checkLimit(limit) {
        console.log('limit', limit, 'this', this)
    }

    // async loadGoodReceiveData() {
    //     request({
    //         method: 'GET',
    //         url: 'api/goodsReceives/' + this.goodReceive,
    //         data: {}
    //     }).then(response => {
    //         const data = response.data;
    //         this.items = data.data.items;
    //         console.log(this.items)
    //
    //     }).catch(error => {
    //         console.log(error);
    //     })
    // }
    dismiss() {
        this.navCtrl.pop();
    }

    validateForm(): any {
        return new Promise((resolve, reject) => {
            /**
             * Check at least 1 item qty
             */
            let least_one_item = _.some(this.goodReceive.items, (item) => {
                return item.receiveQty > 0;
            });
            if (!least_one_item) {
                return resolve({
                    error: true,
                    message: this.translate.instant('Must input receive qty atleast 1 item')
                })
            }


            /**
             * Qty should less than order qty
             */
            let valid_receive_qty = _.every(this.goodReceive.items, (item) => {
                return item.receiveQty <= item.poQty;
            });
            if (!valid_receive_qty) {
                return resolve({
                    error: true,
                    message: this.translate.instant('ReceiveQty should less than or equal OrderQty ')
                })
            }

            resolve({})
        });
    }

    resetData() {
        this.goodReceive.items = _.map(this.goodReceive.items, item => {
            return {
                ...item,
                receiveQty: ''
            }
        })
    }

    async saveData() {
        const form_valid = await this.validateForm();
        if (form_valid.error === true) {
            const alert = this.alertCtrl.create({
                title: this.translate.instant('Error'),
                message: form_valid.message
            }).present();
            return false;
        }

        const receiveNote = this.modalCtrl.create(GoodReceiveDetailSavePage, {
            goodReceive: this.goodReceive
        });
        receiveNote.onDidDismiss((data) => {
            if (data && data.success === true) {
                this.navCtrl.pop();
            }
        })
        receiveNote.present();

    }

}



