import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {request} from "../../providers/rest-api/rest-api";

/**
 * Generated class for the PurchaseOrderPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-purchase-order',
    templateUrl: 'purchase-order.html',
})
export class PurchaseOrderPage {

    purchasingOrders: {
        poHeaderId: string
        totalValue: number
        vendorName: string
    }[] = [];

    constructor(public navCtrl: NavController) {

    }

    loadData() {
        request({
            method: 'POST',
            url: 'api/purchasingOrders',
            data: {
                start: 0,
                length: 0,
            }
        }).then(response => {
            const data = response.data;
            this.purchasingOrders = data.data.purchasingOrders;
            console.log(data)
            console.log(this.purchasingOrders)

        }).catch(error => {
            console.log(error);
        })
    }

    ionViewWillEnter() {
        this.loadData();
    }

}



