import {Injectable} from '@angular/core';
import axios from 'axios';
import {HTTP, HTTPResponse} from "@ionic-native/http";
import {Platform} from "ionic-angular";

/*
  Generated class for the RestApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/


export const Api = axios.create({
    baseURL: 'https://ptg-vrm-api.azurewebsites.net/',
    // timeout: 1000,
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
    },
});

export const setUser = (user) => {
    localStorage.setItem('user', JSON.stringify(user));
}

export const getUser = () => {
    return JSON.parse(localStorage.getItem('user'));
}

export const setCredential = (token) => {
    localStorage.setItem('token', token);
};

export const getCredential = () => {
    const token = 'a4d2345a-b7f9-4a5b-bd97-d1edf5046300';

    if (!token) {
        return {}
    }

    return {
        'Authorization': 'Bearer ' + token
    }
};

export const logout = () => {
    localStorage.removeItem('token');
};

export const checkAuth = () => {
    return Api.post('/api/authorizations', {}, {}).then(response => {
        console.log(response.data);
        if (response && response.data && response.data.data) {
            setUser(response.data.data);
            return true;
        } else {
            return false;
        }
    }).catch(response => {
        return false;
    });
};

export const login = (username, password) => {
    const basic_username = 'vrm-client';
    const basic_password = 'P@ssw0rds';
    const params = {
        method: 'POST',
        url: 'https://ptg-vrm-api.azurewebsites.net/oauth/token',
        auth: {
            username: basic_username,
            password: basic_password,
        },
        params: {
            username,
            password,
            grant_type: 'password',
            client_id: 'vrm-client',
        },
        ...getCredential()
    }
    console.log('Login with', JSON.stringify(params));
    return request(params).then(response => {
        if (response && response.data && response.data.access_token) {
            return response.data.access_token;
        } else {
            return false;
        }
    }).catch((response) => {
        console.log('login error ' + response);
        return false;
    });
};

export const request = (config) => {
    return Api.request({
        ...config,
        ...getCredential()
    });
}


export default Api;

@Injectable()
export class RestApiProvider {

    constructor(public http: HTTP,
                public platform: Platform) {
        console.log('Hello RestApiProvider Provider');
        this.http.setDataSerializer('json');
    }

    static getPath(path = '') {
        return 'https://ptg-vrm-api.azurewebsites.net/' + path;
    }

    static getHeader(headers_options = {}) {
        let headers = {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            ...getCredential(),
            ...headers_options
        };
        return headers;
    }

    static parseJSON = (response: HTTPResponse) => {
        try {
            console.log('DATA | ' + response.data);
            return JSON.parse(response.data);
        } catch (ex) {
            console.log('Parse json error', ex)
            return {}
        }
    }

    get(path: string, params: any = {}, headers: any = {}) {
        const request_path = RestApiProvider.getPath(path);
        const request_headers = RestApiProvider.getHeader(headers);

        console.log('POST | ' + request_path)
        console.log('HEADER | ' + JSON.stringify(request_headers));
        console.log('DATA | ' + JSON.stringify(params));

        if (this.platform.is('cordova')) {
            return this.http.get(
                request_path,
                params,
                request_headers)
                .then(RestApiProvider.parseJSON)
        } else {
            return axios.request({
                method: 'GET',
                url: request_path,
                params,
                headers: request_headers
            }).then(response => response.data)
        }
        // .catch(error => {
        //     console.log('GET ERROR' + JSON.stringify(error))
        // })
    }

    post(path: string, params: any = {}, headers: any = {}) {
        const request_path = RestApiProvider.getPath(path);
        const request_headers = RestApiProvider.getHeader(headers);

        console.log('POST | ' + request_path)
        console.log('HEADER | ' + JSON.stringify(request_headers));
        console.log('DATA | ' + JSON.stringify(params));

        if (this.platform.is('cordova')) {
            return this.http.post(
                request_path,
                params,
                request_headers)
                .then(RestApiProvider.parseJSON)
        } else {

            return axios.request({
                method: 'POST',
                url: request_path,
                data: params,
                headers: request_headers
            }).then(response => response.data)
        }
        // .catch(error => {
        //     console.log('POST ERROR' + JSON.stringify(error))
        // })
    }

}



