import {Component, ViewChild} from '@angular/core';
import {App, IonicPage, Nav, NavController, NavParams, Platform} from 'ionic-angular';
import {AuthProvider} from "../../providers/auth/auth";
import {LoginPage} from "../login/login";
import {HomePage} from "../home/home";
import {GoodReceivePage} from "../good-receive/good-receive";
import {TranslateService} from "@ngx-translate/core";

@IonicPage()
@Component({
    selector: 'page-menu',
    templateUrl: 'menu.html',
})
export class MenuPage {
    rootPage: any;
    pages = [];
    username = '';

    // Reference to the side menus root nav
    @ViewChild(Nav) nav: Nav;

    constructor(public navCtrl: NavController,
                private authProvider: AuthProvider,
                private translate: TranslateService,
                private appCtrl: App) {
    }

    ionViewWillEnter() {
        this.pages = [
            // {title: 'Home', page: HomePage, icon: 'home'},
            {title: 'GR', page: GoodReceivePage, icon: 'planet'}
        ];
        // if (this.authProvider.isAdmin()) {
        //     this.pages = [
        //         {title: 'Admin Dashboard', page: 'AdminPage', icon: 'home'},
        //         {title: 'Admin Second Page', page: 'AdminSecondPage', icon: 'planet'}
        //     ];
        //     this.openPage('AdminPage');
        // } else {
        //     this.pages = [
        //         {title: 'User Dashboard', page: 'UserPage', icon: 'home'},
        //         {title: 'User Second Page', page: 'UserSecondPage', icon: 'planet'}
        //     ];
        //     this.openPage('UserPage');
        // }
        this.username = this.authProvider.currentUser.name;
        this.openPage(GoodReceivePage)
    }

    changeLocale(locale) {
        this.translate.use(locale);
    }

    logout() {
        this.authProvider.logout();
        this.appCtrl.getRootNav().setRoot(LoginPage);
    }

    openPage(page) {
        this.nav.setRoot(page);
    }

    ionViewCanEnter() {
        return this.authProvider.isLoggedIn();
    }

}



