import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {request} from "../../providers/rest-api/rest-api";

/**
 * Generated class for the PurchaseRequestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-purchase-request',
    templateUrl: 'purchase-request.html',
})
export class PurchaseRequestPage {

    purchasingRequests: {
        prHeaderId: string,
        createdBy: string,
        totalValue: number
    }[] = [];

    constructor(public navCtrl: NavController, public navParams: NavParams) {
    }

    ionViewWillEnter() {
        this.loadData();
    }

    loadData() {
        request({
            method: 'POST',
            url: 'api/purchasingRequests',
            data: {
                start: 0,
                length: 0,
            }
        }).then(response => {
            const data = response.data;
            this.purchasingRequests = data.data.purchasingRequests;
            console.log(data)
            console.log(this.purchasingRequests)

        }).catch(error => {
            console.log(error);
        })
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad PurchaseRequestPage');
    }

}



