import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {HTTP} from "@ionic-native/http";
import qs from 'qs';
import axios from 'axios';
import {RestApiProvider, setCredential} from "../rest-api/rest-api";
import {Platform} from "ionic-angular";

export interface User {
    name: string;
    role: number;
}

/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider {
    currentUser: User;

    auth = {
        username: 'vrm-client',
        password: 'P@ssw0rds',
        params: {
            grant_type: 'password',
            client_id: 'vrm-client',
        }
    }

    constructor(public http: HTTP,
                public restApi: RestApiProvider,
                public platform: Platform) {
        console.log('Hello AuthProvider Provider');
    }

    login(username: string, password: string): Promise<boolean> {

        const path = 'https://ptg-vrm-api.azurewebsites.net/oauth/token?' +
            qs.stringify({
                ...this.auth.params,
                username, password
            });

        if (this.platform.is('cordova')) {

            this.http.setDataSerializer('json');
            this.http.useBasicAuth(this.auth.username, this.auth.password);

            console.log('path', path);

            return this.http.post(path,
                {}, {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                }).then((response: any) => {
                this.currentUser = {
                    name: 'User',
                    role: 0,
                }

                const data = JSON.parse(response.data);
                console.log('success', data);
                if (data && data.access_token) {
                    setCredential(data.access_token);
                    return true;
                } else {
                    return false;
                }
            }).catch((error) => {
                console.log('error');
                console.log(JSON.stringify(error));
                return false;
            })
        } else {
            return axios.request({
                method: 'post',
                url: path,
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                auth: {
                    username: this.auth.username,
                    password: this.auth.password
                }
            }).then((response) => {
                this.currentUser = {
                    name: 'User',
                    role: 0,
                }

                const data = response.data;
                console.log('success', data);
                if (data && data.access_token) {
                    setCredential(data.access_token);
                    return true;
                } else {
                    return false;
                }
            }).catch(() => {
                return false;
            })
        }


    }

    isLoggedIn() {
        return this.currentUser != null;
    }

    isAdmin() {
        return this.currentUser.role === 0;
    }

    logout() {
        this.currentUser = null;
    }
}



