import {NgModule, ErrorHandler} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {IonicApp, IonicModule, IonicErrorHandler} from 'ionic-angular';
import {MyApp} from './app.component';

import {AboutPage} from '../pages/about/about';
import {ContactPage} from '../pages/contact/contact';
import {HomePage} from '../pages/home/home';
import {TabsPage} from '../pages/tabs/tabs';

import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {NgxQRCodeModule} from "ngx-qrcode2";
import {BarcodeScanner} from "@ionic-native/barcode-scanner";
import {RestApiProvider} from '../providers/rest-api/rest-api';
import {AuthPage} from "../pages/auth/auth";
import {PurchaseOrderPage} from "../pages/purchase-order/purchase-order";
import {GoodReceivePage} from "../pages/good-receive/good-receive";
import {PurchaseRequestPage} from "../pages/purchase-request/purchase-request";
import {CallbackPipe} from "../pipes/callback";
import {GoodReceiveDetailPage} from "../pages/good-receive-detail/good-receive-detail";
import {HTTP} from '@ionic-native/http';
import {LoginPage} from "../pages/login/login";
import {CustomFormsModule} from "ng2-validation";
import {FormsModule} from "@angular/forms";
import {TranslateLoader, TranslateModule} from "@ngx-translate/core";
import {HttpClient, HttpClientModule} from "@angular/common/http";
import {TranslateHttpLoader} from "@ngx-translate/http-loader";
import {MenuPage} from "../pages/menu/menu";
import {AuthProvider} from '../providers/auth/auth';
import {GoodReceiveDetailSavePage} from '../pages/good-receive-detail-save/good-receive-detail-save';
import {AppHeaderComponent} from "../components/app-header/app-header";
import {ComponentsModule} from "../components/components.module";

export function createTranslateLoader(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
    declarations: [
        MyApp,
        MenuPage,
        LoginPage,
        AboutPage,
        ContactPage,
        HomePage,
        TabsPage,
        AuthPage,
        PurchaseOrderPage,
        PurchaseRequestPage,
        GoodReceivePage,
        GoodReceiveDetailPage,
        GoodReceiveDetailSavePage,
        CallbackPipe,
    ],
    imports: [
        BrowserModule,
        IonicModule.forRoot(MyApp),
        HttpClientModule,
        NgxQRCodeModule,
        FormsModule,
        CustomFormsModule,
        ComponentsModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: (createTranslateLoader),
                deps: [HttpClient]
            }
        })
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        MenuPage,
        LoginPage,
        AboutPage,
        ContactPage,
        HomePage,
        TabsPage,
        AuthPage,
        PurchaseOrderPage,
        PurchaseRequestPage,
        GoodReceivePage,
        GoodReceiveDetailPage,
        GoodReceiveDetailSavePage,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        BarcodeScanner,
        RestApiProvider,
        HTTP,
        RestApiProvider,
        AuthProvider,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
    ]
})
export class AppModule {
}



