import {Component} from '@angular/core';
import {AlertController, App, IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {request, RestApiProvider} from "../../providers/rest-api/rest-api";
import {TranslateService} from "@ngx-translate/core";
import {GoodReceivePage} from "../good-receive/good-receive";

/**
 * Generated class for the GoodReceiveDetailSavePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-good-receive-detail-save',
    templateUrl: 'good-receive-detail-save.html',
})
export class GoodReceiveDetailSavePage {

	isenabled:boolean=true;
	
    form = {
        documentDate: '',
        documentNote: '',
        note: '',
    }

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public viewCtrl: ViewController,
                public alertCtrl: AlertController,
                public translate: TranslateService,
                public restApi: RestApiProvider,
                public app: App) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad GoodReceiveDetailSavePage');
    }

    dismiss() {
        this.viewCtrl.dismiss({
            cancel: true
        });
    }

    async saveData() {
		
		this.isenabled=false;
		
        const goodReceive = this.navParams.get('goodReceive') || {}
        const data = {
            createDate: '',
            deliveryNote: '',
            documentDate: '',
            note: '',
            postingDate: '',
            vendorId: '',
            ...goodReceive,
            ...this.form
        }

        const result = await this.saveGRItems(data) || {};
        console.log('save result', result);
        if (result.matDoc) {
            const alert = this.alertCtrl.create({
                title: this.translate.instant('Success'),
                message: this.translate.instant("Material Document") + ": " + result.matDoc
            });
            alert.onDidDismiss(() => {
                this.viewCtrl.dismiss({success: true});
            });
            alert.present();
        } else if (result.error === true) {
            this.alertCtrl.create({
                title: this.translate.instant('Error'),
                message: result.message
            }).present();
        } else {
            this.alertCtrl.create({
                title: this.translate.instant('Error'),
                message: this.translate.instant('Unknown error')
            }).present();
        }
    }

    saveGRItems = async (params) => {
        return this.restApi.post(
            'api/goodsReceives/save',
            params
        ).then(data => {
            console.log('save', data);
            if (data.success && data.data && data.data.matDoc) {
                return data.data;
            } else {
                return {
                    error: true,
                    message: data.msg[this.translate.currentLang]
                }
            }
        }).catch(error => {
            console.log(error);
            return {
                error: true,
                message: this.translate.instant('Unknown Error')
            }
        })
    }

}



